@import '../../../../constants/global.scss';

.Sentence {
  font-family: 'Merriweather';
  line-height: 36px;

  input {
    border: 0px;
    border-bottom: 1px solid $color-gray-3;
    font-size: $font-size-medium;
    font-family: 'Catamaran';
    outline: none;
    text-align: center;
    height: 26px;
  }

  input:focus {
    border-bottom-width: 2px;
    border-bottom-color: $color-gray-2;
  }

  input::placeholder {
    color: $color-gray-4;
  }

  .correct {
    color: $color-success;
    border-bottom-color: $color-success !important;
    border-bottom-width: 2px !important;
    background-color: lighten($color: $color-success, $amount: 50);
  }

  .incorrect {
    color: $color-danger;
    border-bottom-color: $color-danger !important;
    border-bottom-width: 2px !important;
    background-color: lighten($color: $color-danger, $amount: 40);
  }

  .preview {
    font-weight: bolder;
    color: $color-success;
  }

  .correctAnswer {
    font-weight: bolder;
    color: $color-success;
    margin-left: 5px;
  }
}
