.Label__highlighted {
  color: red;
  border: 1px solid red;
  border-radius: 4px;
  cursor: pointer;
}

.Label__highlighted:hover {
  border-width: 2px;
}

.Label__highlighted__tracked {
  color: green;
  border-color: green;
}

.Label__highlighted__tracked__in__context {
  color: orange;
  border-color: orange;
}
