@import '../constants/global.scss';

.Header {
  color: #fff;
  height: 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.Header__left {
  display: flex;
  align-items: center;
}

.Header__icon {
  padding: 0 20px;
}

.Header__title {
  font-size: $font-size-large;
  line-height: $line-height;
}

.Header__right {
  height: 100%;
  margin: 0 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.Header__closeIcon {
  margin-left: 20px;
}

.Header__logo {
  height: 100%;
  width: 125px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}
