@import '../../constants/global.scss';

.ContainerPage {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
}

.ContainerPage__container {
  width: 90%;
  max-width: 760px;
}

.ContainerPage__title {
  display: flex;
}

.ContainerPage__title > h1,
.ContainerPage__title > h2 {
  font-weight: 700;
  padding-bottom: 2px;
  margin-bottom: 20px;
  border-bottom: 2px solid;
}

.ContainerPage__body {
  background-color: yellow;
  padding: 30px;
  background-color: #fcfcfc;
  box-shadow: 0px 0px 10px #0000000d;
  border: 1px solid #dddddd;
  border-radius: 4px;
  margin-bottom: 50px;
}
