@import '../../constants/global.scss';

.Agreement {
  @extend %fadeIn;

  hr {
    border: 0;
    border-bottom: 1px solid $color-gray-6;
  }
}

.Agreement__checkbox {
  display: flex;
  justify-content: center;
  padding: 20px;
  font-family: 'Merriweather';
  margin-bottom: 20px;

  input,
  label {
    @extend %clickable;
  }
}

.Agreement__footer {
  display: flex;
  justify-content: center;
}

.Agreement__intro > p {
  text-align: center;
  margin-bottom: 30px;
  font-family: 'Merriweather';
}
