@import '../../constants/global.scss';

.Radio {
  display: flex;
  padding: 10px;

  label,
  input {
    @extend %clickable;
  }
}

.Radio > input {
  margin-top: 8px;
  margin-right: 8px;
}

.Radio.--correct,
.Radio.--incorrect,
.Radio.--showAnswer {
  border-radius: 6px;
  font-weight: 700;
}

.Radio.--correct {
  color: $color-success;
  background-color: $color-success-light;
  border: 1px solid $color-success;
}

.Radio.--incorrect {
  color: $color-danger;
  background-color: $color-danger-light;
  border: 1px solid $color-danger;
}

.Radio.--showAnswer {
  color: $color-success;
}
