@import '../../constants/global.scss';

.Game {
  @extend %fadeIn;
}

// when in mobile AND orientation landscape, shows fullscreen
@media (max-width: #{($desktop-width - 1)}) and (orientation:landscape) {
  .Game__iframe {
    position: fixed;
    left: 0;
    top: 0;
    background-color: #000;
  }
}
