@import '../../constants/global.scss';

.Feedback {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 10px;
  border: 0 solid $color-gray-5;
  border-bottom-width: 1px;
  border-top-width: 1px;
  background-color: $color-gray-7;
}

.Feedback__type {
  text-align: center;
  padding: 0 20px;
  font-weight: 700;
  text-transform: uppercase;
  font-size: $font-size-mini;
  line-height: 26px;
}

.Feedback__icon {
  font-size: $font-size-huge;
}

.Feedback__text {
  flex: 1;
  line-height: $line-height;
}

.Feedback.--correct .Feedback__type {
  color: $color-success;
}

.Feedback.--incorrect .Feedback__type {
  color: $color-danger;
}
