.Tooltip {
  position: relative;
  display: inline-block;
}

// to set max-width:
// https://github.com/wwayne/react-tooltip/issues/651
// ps: had to disable overflow to display arrow
.ReactTooltip {
  max-width: 200px;
  white-space: pre-wrap;
  // overflow: hidden;
  text-align: center;
  box-sizing: content-box;
}

.ReactTooltip__html {
  height: 20px;
}

.tooltipHtmlWrapper {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;

  > div {
    width: 8px;
    height: 8px;
    background-color: #51ff7a;
    margin-right: 8px;
    border-radius: 8px;
    animation: blinker 1.5s linear infinite;
  }
}

@keyframes blinker {
  50% {
    opacity: 0;
  }
}
