$color-gray-1: #333;
$color-gray-2: #666;
$color-gray-3: #999;
$color-gray-4: #ccc;
$color-gray-5: #ddd;
$color-gray-6: #eee;
$color-gray-7: #f7f7f7;

$color-success: #21ba45;
$color-warning: #ffc107;
$color-danger: #dc3545;
$color-info: #0064c8;

$color-success-light: #21ba4515;
$color-warning-light: #ffc10715;
$color-danger-light: #dc354515;
$color-info-light: #0064c815;

$font-size-xmini: 0.7rem;
$font-size-mini: 0.78571429rem;
$font-size-tiny: 0.85714286rem;
$font-size-small: 0.92857143rem;
$font-size-medium: 1rem;
$font-size-large: 1.14285714rem;
$font-size-big: 1.28571429rem;
$font-size-huge: 1.42857143rem;
$font-size-massive: 1.71428571rem;

$letter-spacing: 2px;

$line-height: 18px;

%clickable {
  cursor: pointer;

  /* noselect */
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently
                          supported by Chrome, Opera and Firefox */
}

$desktop-width: 576px;

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

%fadeIn {
  -webkit-animation: fadein 0.3s; /* Safari, Chrome and Opera > 12.1 */
  -moz-animation: fadein 0.3s; /* Firefox < 16 */
  -ms-animation: fadein 0.3s; /* Internet Explorer */
  -o-animation: fadein 0.3s; /* Opera < 12.1 */
  animation: fadein 0.3s;
}
