.itemWrapper {
  display: flex;
}

.item {
  user-select: none;
  padding: 16px;
  margin: 0;
  background: white;
  border: 1px solid #ddd;
  border-left-width: 5px !important;
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;

  svg {
    color: #ccc;
    margin-right: 5px;
  }
}

.itemWithArrow {
  position: relative;
}
.itemWithArrow:after,
.itemWithArrow:before {
  left: 100%;
  top: 50%;
  border: solid transparent;
  content: '';
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}

.itemWithArrow:after {
  border-color: rgba(255, 255, 255, 0);
  border-left-color: #ffffff;
  border-width: 10px;
  margin-top: -10px;
}
.itemWithArrow:before {
  border-color: rgba(204, 204, 204, 0);
  border-left-color: #ccc;
  border-width: 11px;
  margin-top: -11px;
}

.itemIsDragging {
  border-color: #999;
  box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.1);
}

.itemIsDragging.itemWithArrow:before {
  border-left-color: #999;
}

.itemIsDraggingOver {
  opacity: 0;
  animation: fadeOut 0.3s ease-out;
}

.itemIsCorrect {
  color: #21ba45;
  border-color: #21ba45;
  background-color: lighten($color: #21ba45, $amount: 50);
}

.itemIsIncorrect {
  color: #dc3545;
  border-color: #dc3545;
  background-color: lighten($color: #dc3545, $amount: 40);
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
