@import '../../constants/global.scss';

.Button {
  background-color: #ffffff40;
  color: #fff;
  padding: 8px 20px;
  border-radius: 50px;
  text-transform: uppercase;
  letter-spacing: $letter-spacing;
  font-weight: 700;
  font-size: $font-size-mini;
  min-width: 110px;
  text-align: center;
  @extend %clickable;
}

.Button.--primary {
  color: #fff;
}

.Button.--secondary {
  background-color: #fff;
  border: 2px solid;
}

.Button.--disabled {
  background-color: $color-gray-5;
  opacity: 0.1;
  cursor: not-allowed;
}
