@import '../constants/global.scss';

.NavFooter {
  color: #fff;
  min-height: 70px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
  flex-wrap: wrap;
}

.NavFooter__audio {
  display: flex;
  justify-content: center;
  flex: 1;
  padding: 10px 0;
}

.NavFooter__audio > audio {
  flex-grow: 1;
  height: 30px;
}

.NavFooter__navbuttons {
  display: flex;
  justify-content: space-between;
  flex: 1;
  padding: 10px 0;
}

.NavFooter__progress {
  display: none;
  max-width: 200px;
  width: 100%;
  padding-top: 10px;
}

.NavFooter__button__blink {
  animation: blinker 1.5s ease-in-out infinite;
}

@keyframes blinker {
  50% {
    background-color: rgba(255, 255, 255, 0.7);
  }
}

.tooltip {
  margin-top: 0px !important;
  max-width: fit-content;
}

@media (min-width: #{$desktop-width}) {
  .NavFooter__progress {
    display: block;
  }

  .NavFooter__button {
    min-width: 150px !important;
  }

  .NavFooter__nextButton {
    margin-left: 10px;
  }

  .NavFooter__navbuttons {
    flex: 0;
  }

  .NavFooter__audio > audio {
    flex: 1;
    margin: 0 10px;
  }
}
