.Alert {
  position: relative;
  padding: 0.75rem 1.25rem;
  margin-bottom: 1rem;
  border: 1px solid transparent;
  border-radius: 0.25rem;
}

.Alert__primary {
  color: #004085;
  background-color: #cce5ff;
  border-color: #b8daff;
}

.Alert__success {
  color: #155724;
  background-color: #d4edda;
  border-color: #c3e6cb;
}

.Alert__warning {
  color: #856404;
  background-color: #fff3cd;
  border-color: #ffeeba;
}

.Alert__danger {
  color: #721c24;
  background-color: #f8d7da;
  border-color: #f5c6cb;
}

.Alert__purple {
  color: purple;
  background-color: lighten($color: purple, $amount: 70);
  border-color: lighten($color: purple, $amount: 65);
}
