@import '../../constants/global.scss';

.PlaceholderSlide {
  position: relative;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.1);
  z-index: 44;
  overflow: hidden;
  @extend %fadeIn;
}

.PlaceholderSlide__activity {
  position: absolute;
  height: 100%;
  width: 100%;
  background-repeat: no-repeat;
  background-image: linear-gradient(
    to left,
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0.08),
    rgba(0, 0, 0, 0)
  );
  background-image: -moz-linear-gradient(
    to left,
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0.08),
    rgba(0, 0, 0, 0)
  );
  background-image: -webkit-linear-gradient(
    to left,
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0.08),
    rgba(0, 0, 0, 0)
  );
  animation: loading 1s infinite;
  z-index: 45;
}

@keyframes loading {
  0% {
    background-position: -1200px;
  }
  100% {
    background-position: 1200px;
  }
}
