@import '../../constants/global.scss';

.ConfirmDialog {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px 50px;

  svg {
    font-size: xx-large;
    border-radius: 50%;
    width: 40px !important;
    height: 40px;
    padding: 5px;
    background-color: white;
  }

  h2 {
    margin-bottom: 0;
  }

  div {
    display: flex;
    margin-top: 15px;

    > * {
      margin: 5px;
    }
  }

  > p {
    text-align: center;
  }
}

.ConfirmDialogSuccess {
  svg {
    color: $color-success;
    border: 4px solid $color-success;
  }
}

.ConfirmDialogDanger {
  svg {
    color: $color-danger;
    border: 4px solid $color-danger;
  }
}

.overlay {
  position: fixed;
  inset: 0px;
  background-color: rgba(0, 0, 0, 0.8);
  -webkit-animation: fadein 0.2s; /* Safari, Chrome and Opera > 12.1 */
  -moz-animation: fadein 0.2s; /* Firefox < 16 */
  -ms-animation: fadein 0.2s; /* Internet Explorer */
  -o-animation: fadein 0.2s; /* Opera < 12.1 */
  animation: fadein 0.2s;
}

.content {
  position: absolute;
  inset: 40px;
  border: 1px solid rgb(204, 204, 204);
  background: rgb(255, 255, 255);
  overflow: auto;
  border-radius: 4px;
  outline: none;
  padding: 20px;
  max-width: 500px;

  top: 50%;
  left: 50%;
  right: auto;
  bottom: auto;
  margin-right: -50%;
  transform: translate(-50%, -50%);

  -webkit-animation: fadein 0.2s; /* Safari, Chrome and Opera > 12.1 */
  -moz-animation: fadein 0.2s; /* Firefox < 16 */
  -ms-animation: fadein 0.2s; /* Internet Explorer */
  -o-animation: fadein 0.2s; /* Opera < 12.1 */
  animation: fadein 0.2s;
}

.portal {
  z-index: 2;
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
