@import '../../constants/global.scss';

.overlay {
  position: fixed;
  inset: 0px;
  background-color: rgba(0, 0, 0, 0.9);
  -webkit-animation: fadein 0.2s; /* Safari, Chrome and Opera > 12.1 */
  -moz-animation: fadein 0.2s; /* Firefox < 16 */
  -ms-animation: fadein 0.2s; /* Internet Explorer */
  -o-animation: fadein 0.2s; /* Opera < 12.1 */
  animation: fadein 0.2s;
}

.content {
  position: absolute;
  inset: 40px;
  border: 1px solid rgb(204, 204, 204);
  background: rgb(255, 255, 255);
  overflow: auto;
  border-radius: 4px;
  outline: none;
  padding: 20px;
  max-width: 500px;

  top: 50%;
  left: 50%;
  right: auto;
  bottom: auto;
  margin-right: -50%;
  transform: translate(-50%, -50%);

  -webkit-animation: fadein 0.2s; /* Safari, Chrome and Opera > 12.1 */
  -moz-animation: fadein 0.2s; /* Firefox < 16 */
  -ms-animation: fadein 0.2s; /* Internet Explorer */
  -o-animation: fadein 0.2s; /* Opera < 12.1 */
  animation: fadein 0.2s;
}

.portal {
  z-index: 2;
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.messageContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px;

  > p {
    font-weight: bold;
    font-size: x-large;
    margin: 0;
  }

  > span {
    text-align: center;
    line-height: 1.4rem;
  }

  hr {
    width: 100%;
    border: 0;
    border-bottom: 0.5px solid $color-gray-5;
    margin: 15px;
  }

  .licenseInfo {
    font-size: small;
    color: $color-gray-3;
    padding: 0;
    line-height: 1rem;
  }

  .warningIcon,
  .dangerIcon {
    > svg {
      font-size: xx-large;
      border-radius: 50%;
      width: 40px !important;
      height: 40px;
      padding: 5px;
      background-color: white;
    }
  }

  .warningIcon {
    > svg {
      color: $color-warning;
      border: 4px solid $color-warning;
    }
  }

  .dangerIcon {
    > svg {
      color: $color-danger;
      border: 4px solid $color-danger;
    }
  }

  .okButton {
    margin-top: 20px;
    border: 2px solid $color-gray-2;
    background-color: $color-gray-2 !important;
    justify-content: center;
  }
}
