@import '../../../../constants/global.scss';

.Essay {
  padding: 20px 0;

  textarea {
    width: 100%;
    border: 1px solid $color-gray-5;
    border-radius: 4px;
    display: block;
    line-height: 20px;
    padding: 7px;
    outline: none;
    font-size: $font-size-small;
    font-family: 'Catamaran';
  }

  textarea:focus {
    border: 1px solid lighten($color: $color-gray-2, $amount: 20);
    box-shadow: 0 0rem 0.25rem lighten($color: $color-gray-2, $amount: 40);
  }

  textarea::placeholder {
    color: $color-gray-4;
  }
}

.Essay__correct {
  textarea {
    color: $color-success;
    border: 1px solid lighten($color: $color-success, $amount: 20);
    box-shadow: 0 0rem 0.25rem lighten($color: $color-success, $amount: 40);
    background-color: lighten($color: $color-success, $amount: 55);
  }
}

.Essay__incorrect {
  textarea {
    color: $color-danger;
    border: 1px solid lighten($color: $color-danger, $amount: 20);
    box-shadow: 0 0rem 0.25rem lighten($color: $color-danger, $amount: 40);
    background-color: lighten($color: $color-danger, $amount: 45);
  }
}

.acceptableResponses {
  margin: 10px 0;
  font-style: italic;
  color: $color-gray-2;
  font-size: $font-size-small;
}
