@import '../../constants/global.scss';

.root {
  font-family: 'Catamaran';
  background-color: #fff;
  color: #333;
  padding: 5px 10px;
  border-radius: 5px;
  text-transform: uppercase;
  letter-spacing: 0.1rem;
  font-weight: 700;
  font-size: small;
  text-align: center;
  border: 1px solid #333;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  // @extend %clickable;

  .icon {
    font-size: large;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .mr {
    margin-right: 10px;
  }
}

.gray {
  background-color: #ddd;
  border-color: #ddd;
}

.disabled {
  opacity: 0.25;
}

.root:hover {
  background-color: #f7f7f7;
}

.root:active {
  transform: translateY(1px);
}
