@import '../../constants/global.scss';

.root {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 100%; // flex: 1;
  border-top: 1px solid $color-gray-5;
  padding-top: 10px;

  .detail {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 5px;

    .badgeMain {
      font-size: x-large;
      padding: 8px;
    }

    .badge {
      margin: 0 5px;
      display: inline-block;
      min-width: 35px !important;
      text-align: center;
    }
  }

  .hidden {
    display: none;
  }

  .iconToggleExpand {
    color: $color-gray-4;
    margin-top: 3px;
  }

  .alert {
    margin: 0;
    font-size: small;
    padding: 6px 12px;
  }
}
