@import '../../constants/global.scss';

.Quiz {
  @extend %fadeIn;
}

.Quiz__question {
  font-family: 'Merriweather';
  line-height: 30px;
}

.Quiz__question {
  padding: 20px 0;
  border-bottom: 1px solid $color-gray-5;
}

.Quiz__footer {
  display: flex;
  justify-content: center;
}

.Quiz__intro > h3 {
  text-align: center;
  margin: 40px 0;
}

.Quiz__question__component {
  border-bottom: 1px solid $color-gray-4;
  margin-bottom: 30px;
}
