.dropzone {
  min-height: 50px;
  display: flex;
}

.dropzone_source {
  width: 220px;

  > div {
    flex: 1;
  }
}

.dropzone_destination {
  width: 440px;
  justify-content: flex-end;

  > div {
    flex-basis: 50%;
  }
}

@media only screen and (max-width: 768px) {
  .dropzone_source {
    max-width: 50%;
  }

  .dropzone_destination {
    max-width: 100%;
  }
}

.dropzoneIsDragging {
  background-color: #eee;

  .droppableTextWrapper {
    border-color: #999;
  }

  .droppableTextWrapperWithArrow:after {
    background-color: #eee;
  }

  .droppableTextWrapperWithArrow:before {
    background-color: #999;
  }
}

.droppableTextWrapper {
  background-color: white;
  border: 1px solid #ddd;
  display: flex;
  justify-content: center;
  align-items: center;
}

.droppableTextWrapperWithArrow {
  position: relative;
}
.droppableTextWrapperWithArrow:after,
.droppableTextWrapperWithArrow:before {
  content: '';
  position: absolute;
  left: -10px;
  top: 50%;
  margin-top: -8px;
  width: 16px;
  height: 16px;
  background-color: white;
  transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
}
.droppableTextWrapperWithArrow:before {
  left: -9px;
  background-color: #ddd;
}

.droppableTextWrapperNoLeftBorder {
  border-left-width: 0;
}

.droppableTextWrapperCorrect {
  color: #21ba45;
  border-color: #21ba45;
  background-color: lighten($color: #21ba45, $amount: 50);
}

.droppableTextWrapperCorrect:before {
  background-color: #21ba45;
}

.droppableTextWrapperCorrect:after {
  background-color: lighten($color: #21ba45, $amount: 50);
}

.droppableTextWrapperIncorrect {
  color: #dc3545;
  border-color: #dc3545;
  background-color: lighten($color: #dc3545, $amount: 40);
}

.droppableTextWrapperIncorrect:before {
  background-color: #dc3545;
}

.droppableTextWrapperIncorrect:after {
  background-color: lighten($color: #dc3545, $amount: 40);
}

.text {
  margin: 16px;
}
