@import '../constants/global.scss';

.Menu {
  background-color: #eee;
  width: 70%;
  height: 100%;
  box-shadow: 5px 0px 15px 5px #00000020;
  position: absolute;
  display: flex;
  flex-direction: column;
}

.Menu__title {
  text-transform: uppercase;
  letter-spacing: $letter-spacing;
  font-size: $font-size-mini;
  font-weight: 700;
  margin: 0 20px;
  margin-bottom: 10px;
  padding-top: 20px;
  padding-bottom: 10px;
  border-bottom: 1px solid #ddd;
}

.Menu__list {
  overflow-y: scroll;
  flex: 1;
}

.Menu__bottom {
  display: flex;
  font-size: $font-size-mini;
  color: #ccc;
  margin-left: 5px;
}

@media (min-width: #{$desktop-width}) {
  .Menu {
    position: relative;
    width: 100%;
    box-shadow: inset -35px 0px 35px -35px #00000020;
  }
}
