@import '../../constants/global.scss';

.Badge {
  background-color: #cce5ff;
  color: #5469d4;
  font-weight: 600;
  padding: 2px 6px;
  border-radius: 4px;
  font-size: 0.8rem;
}

.success {
  background-color: #cbf4c9;
  color: #0e6245;
}
.success.darken {
  background-color: #096f18;
  color: #cbf4c9;
}

.warning {
  background-color: #fff3cd;
  color: #856404;
}
.warning.darken {
  background-color: #856404;
  color: #fff3cd;
}

.danger {
  background-color: #fde2dd;
  color: #a41c4e;
}
.danger.darken {
  background-color: #da0000;
  color: #fde2dd;
}

.cancel {
  background-color: #e3e8ee;
  color: #4f566b;
}
.cancel.darken {
  background-color: #4f566b;
  color: #e3e8ee;
}

.purple {
  background-color: lighten($color: purple, $amount: 70);
  color: darken($color: purple, $amount: 0);
}
.purple.darken {
  background-color: darken($color: purple, $amount: 0);
  color: lighten($color: purple, $amount: 70);
}

.clickable {
  @extend %clickable;
}
