* {
  box-sizing: border-box;
}

.CoursePlayer {
  font-family: 'Catamaran';
  color: #333;
  display: flex;
  justify-content: space-between;
  height: 100%;
}

.CoursePlayer__main {
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: space-between;
}

.CoursePlayer__menu {
  width: 320px;
  margin-left: 0;
  transition: margin-left 0.4s;
  overflow: hidden;
}

.CoursePlayer__menu--hidden {
  margin-left: -320px;
}
