.modalOverlayPrintDialogOpen {
  // default from modal
  position: fixed;
  inset: 0px;
  background-color: rgba(0, 0, 0, 0.8);
  -webkit-animation: fadein 0.2s; /* Safari, Chrome and Opera > 12.1 */
  -moz-animation: fadein 0.2s; /* Firefox < 16 */
  -ms-animation: fadein 0.2s; /* Internet Explorer */
  -o-animation: fadein 0.2s; /* Opera < 12.1 */
  animation: fadein 0.2s;

  // specific for this one
  z-index: 2;
}

.modalContent,
.modalContentInEditMode {
  // default from modal
  position: absolute;
  inset: 40px;
  border: 1px solid rgb(204, 204, 204);
  background: rgb(255, 255, 255);
  overflow: auto;
  border-radius: 4px;
  outline: none;
  padding: 20px;

  // specific for this one
  display: flex;
  flex-direction: column;
}

.modalContentInEditMode {
  margin-left: 380px; // to allow edit properties on left menu
}

.topMenu {
  display: flex;
  margin-bottom: 10px;

  > :first-child {
    margin-right: 5px;
  }

  > :last-child {
    margin-left: auto;
  }
}

@media print {
  .topMenu {
    display: none;
  }
}
