.root {
  //   border: 80px solid #c40000;
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  font-family: Georgia, serif;
  padding: 80px;
  background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0 0h16v2h-6v6h6v8H8v-6H2v6H0V0zm4 4h2v2H4V4zm8 8h2v2h-2v-2zm-8 0h2v2H4v-2zm8-8h2v2h-2V4z' fill='%23999999' fill-opacity='1' fill-rule='evenodd'/%3E%3C/svg%3E");
  background-color: #ddd;

  .inner {
    flex: inherit;
    display: inherit;
    flex-direction: inherit;
    justify-content: inherit;
    align-items: inherit;
    background-color: #fff;
    width: 100%;
    padding: 40px;
    font-family: inherit;

    h2 {
      border-bottom: 1px solid #ccc;
      padding-bottom: 20px;
      text-align: center;
      font-size: 2vw;
      font-family: inherit;
      letter-spacing: -0.1vw;
    }

    h1 {
      font-size: 4vw;
      margin-bottom: 0;
      text-align: center;
      white-space: nowrap;
      font-family: inherit;
      letter-spacing: -0.1vw;
    }

    .main,
    .bottom {
      display: inherit;
      flex-direction: inherit;
      justify-content: inherit;
      align-items: inherit;

      p {
        text-align: center;
      }
    }

    .description {
      font-size: 1.5vw;
    }

    .signature {
      font-family: 'Brush Script MT', cursive;
      font-size: 2.5vw;
      border-bottom: 1px solid #ccc;
      min-width: 25vw;
      margin-bottom: 0;
      color: rgb(0, 0, 150);
      line-height: 1.6vw;
    }

    .instructor {
      margin-top: 10px;
      font-style: italic;
      font-size: 1.25vw;

      span:first-child {
        font-weight: bold;
      }
    }
  }
}

@media print {
  .root {
    -webkit-print-color-adjust: exact;
  }
}
