@import '../../constants/global.scss';

.MenuItem {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 8px;
  border-left: 3px solid #ffffff00;
  @extend %clickable;
}

.MenuItem:hover {
  background-color: #dddddd50;
}

.MenuItem--active {
  background-color: #cccccc50 !important;
}

.MenuItem--disabled {
  color: #ccc !important;
  cursor: not-allowed;
}

.MenuItem__icon {
  margin: 0 10px;
  color: #ccc;
}

.MenuItem__label {
  flex: 1;
  font-size: $font-size-small;
  line-height: $line-height;
}

.MenuItem__completed > svg {
  color: $color-success;
}
