@import '../../constants/global.scss';

.Quiz__result {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
}

.Quiz__result > h2 {
  margin: 0;
}

.Quiz__result__detail {
  display: flex;
  flex-direction: column;
  align-items: center;
  line-height: $line-height;
  padding: 10px;
  margin: 10px;
  border-top: 1px solid $color-gray-5;
  border-bottom: 1px solid $color-gray-5;
}

.Quiz__result__detail > span:nth-child(2) {
  font-weight: 700;
  font-size: 48px;
  margin: 20px;
}

.Quiz__result__passingScore {
  display: flex;
  padding: 10px 0;
}

.Quiz__result__passingScore__passed {
  color: $color-success;
}

.Quiz__result__passingScore__failed {
  color: $color-danger;
}

.Quiz__result__passingScore__icon {
  margin: 0 10px;
}
